import {mapMutations} from 'vuex'
export default {
  props: {
    album: {}
  },
  mounted () {
  },
  methods: {
    ...mapMutations({
      setShow: 'setShow'
    }),
    selectShow () {
      this.setShow(this.album)
      this.$emit('selectShow', this.album)
    }
  }
}



