
import switches from '@/base/switches/switches'
import noResult from '@/base/no-result/no-result'
import showList from '@/base/showList/showList'
import scroll from '@/base/scroll/scroll'
import album from '@/base/album/album'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters, mapMutations } from 'vuex'
export default {
data () {
return {
switchesArray: ['我收藏的', '我听过的'],
currentIndex: 0,
confirmShow: false,
confirm: false,
toastShow: false
}
},
components: {
switches,
noResult,
showList,
album,
scroll,
Confirm,
Toast
},
methods: {
...mapMutations({
clearFavoriteList: 'clearFavoriteList',
clearListenedList: 'clearListenedList',
setFavoriteList: 'setFavoriteList'
}),
back () {
this.$router.back()
},
switched (index) {
this.currentIndex = index
this.confirm = false

if (this.favoriteList.length && this.listenedList.length) {
this.$nextTick(() => {
this.$refs['scroll'].refresh()
})
}
},
onSelectShow (album) {
this.$router.push({
path: `/channel/${album.id}`
})
},
showTip () {
switch (this.currentIndex) {
case 0 :
if (!this.favoriteList.length) {
this.toastShow = true
} else {
this.confirmShow = true
}
break
case 1 :
if (!this.listenedList.length) {
this.toastShow = true
} else {
this.confirmShow = true
}
break
}
},
_clearList () {
this.confirm = true
if (this.confirm) {
switch (this.currentIndex) {
case 0:
this.clearFavoriteList()
break
case 1:
this.clearListenedList()
break
}
}
},
_cancel () {
this.confirmShow = false
},
deleteFromFavorite (album) {
let index = this.favoriteList.findIndex((favorite, index) => {
return favorite.id === album.id
})
let favoriteList = this.favoriteList.concat()
favoriteList.splice(index, 1)
this.setFavoriteList(favoriteList)
}
},
computed: {
...mapGetters(['favoriteList', 'listenedList'])
}
}



